import React, { useState, useEffect, useCallback } from 'react'
import { Link } from 'react-router-dom';
import _ from 'lodash';
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import nfts, { NFT } from 'config/constants/newnfts'
import PageLoader from 'components/PageLoader';
import NftList from './components/NftList'
import NftProvider from './contexts/NftProvider'
import { getNewNftContract } from './utils/contracts'
import filterValues from '../../constants';

const ShibariCollection = () => {

  const [NFTs, setNFTs] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  const [currentFilter, setCurrentFilter] = useState('PRICE_LOWEST');
  const [sortOrder, setSortOrder] = useState(filterValues.PRICE_LOWEST);

  
  const { account } = useWallet()

  const [checkedState, setCheckedState] = useState(
    new Array(6).fill(false)
  );

  const filterNFTs = useCallback(async (filter = '') => {

    setIsLoading(true);

    const newNftContract = getNewNftContract()

    let filteredNFTs = nfts;

    // Inner filter based on format type.
    if(checkedState[0]) {
      filteredNFTs = nfts.filter((nft) => nft.fileType === 'mp4');
    }

    if(checkedState[1]) {
      filteredNFTs = nfts.filter((nft) => nft.fileType === 'jpg');
    }


    const sellableNFTs = [];
    filteredNFTs.forEach(async (nft) => {
      const nftSecondaryMarket = await newNftContract.methods.nftSecondaryMarket(nft.nftId).call()
      const nftInfo = await newNftContract.methods.nftInfo(nft.nftId).call();
      const { authorName, authorTwitter, rarity } = nftInfo;
      const { allowSell, sellMinPrice, totalArtistFee, totalGovernanceFee, lastSellIn, lastSellPrice, qtdSells, totalCollected, totalDevFee } = nftSecondaryMarket;

      if (nftSecondaryMarket.allowSell === true) {
        const sellableTradeIds = await newNftContract.methods.getListOpenTradesByNftId(nft.nftId).call()
        const totalQuantity = Array.isArray(sellableTradeIds) && sellableTradeIds.length

        sellableTradeIds.forEach(async (trade) => {
          const nftSecondaryTradeInfo = await newNftContract.methods.nftSecondaryTradeInfo(trade).call()
          const { sellPrice, sellDate, soldDate } = nftSecondaryTradeInfo;

          const tmpObj = {
            ...nft,
            allowSell,
            sellMinPrice,
            totalArtistFee,
            totalGovernanceFee,
            lastSellIn,
            lastSellPrice,
            qtdSells,
            totalCollected,
            totalDevFee,
            trade,
            totalQuantity,
            authorName,
            authorTwitter,
            sellPrice,
            sellPriceNum: parseFloat(new BigNumber(sellPrice).div(new BigNumber(10).pow(18)).toFixed()), // Used for sorting
            sellDate,
            soldDate,
            rarity
          }

          // Outer Filter based on rarity.
          if((filter === 'RARITY_RARE' && rarity === filterValues.RARITY_RARE)) {
            sellableNFTs.push(tmpObj);
          } else if(filter === 'RARITY_BASE' && rarity === filterValues.RARITY_BASE) {
            sellableNFTs.push(tmpObj);
          } else if (filter === 'PRICE_LOWEST' || filter === 'PRICE_HIGHEST') {
            sellableNFTs.push(tmpObj);
          }

        })
      }
    })

    // Rarity Inner Filters
    setTimeout(() => {

      setNFTs(sellableNFTs);

      let filteredRarityNFTs = [];

      if(checkedState[2]) {
        filteredRarityNFTs = sellableNFTs.filter((nft) => nft.rarity === 'base');
        setNFTs(filteredRarityNFTs);
      }

      if(checkedState[3]) {
        filteredRarityNFTs = sellableNFTs.filter((nft) => nft.rarity === 'rare');
        setNFTs(filteredRarityNFTs);
      }

      if(checkedState[4]) {
        filteredRarityNFTs = sellableNFTs.filter((nft) => nft.rarity === 'epic');
        setNFTs(filteredRarityNFTs);
      }

      if(checkedState[5]) {
        filteredRarityNFTs = sellableNFTs.filter((nft) => nft.rarity === 'legendary');
        setNFTs(filteredRarityNFTs);
      }
    }, 0);

    setTimeout(() => {
      setIsLoading(false);
    }, 1000);

  }, [checkedState])


  const handleFilter = async (filter) => {
    if(filter === 'PRICE_LOWEST') {
      setSortOrder(filterValues.PRICE_LOWEST);
    } else if(filter === 'PRICE_HIGHEST') {
      setSortOrder(filterValues.PRICE_HIGHEST);
    }
    setCurrentFilter(filter);
    await filterNFTs(currentFilter)
  }

  const handleInnerFilter = async (position) => {
    const updatedCheckedState = checkedState.map((item, index) =>
      index === position ? !item : item
    );
    setCheckedState(updatedCheckedState);
    await filterNFTs(currentFilter);
  }

  useEffect(() => {
    filterNFTs(currentFilter)
  }, [filterNFTs, currentFilter, account])

  return (
    <NftProvider>
      <section className="vcollectionsec section-min-height">
        <div className="container">
          <div className="row">
            <div className="col-sm-12 col-md-12 mb-5 vchead">
              <h3>Shibari Collections</h3>

              {/* TODO:: Add filter control when bid is available. */}
              {/* <div className="vctab-wrap">
                <ul className="vctab nav nav-pills mb-3" id="pills-tab" role="tablist">
                  <li className="nav-item" role="presentation">
                    <button className="nav-link vclink active" id="pills-home-tab" data-bs-toggle="pill" data-bs-target="#pills-home" type="button" role="tab" aria-controls="pills-home" aria-selected="true">ALL</button>
                  </li>
                  <li className="nav-item" role="presentation">
                    <button className="nav-link vclink" id="pills-profile-tab" data-bs-toggle="pill" data-bs-target="#pills-profile" type="button" role="tab" aria-controls="pills-profile" >FOR SALE</button>
                  </li>
                  <li className="nav-item" role="presentation">
                    <button className="nav-link vclink" id="pills-contact-tab" data-bs-toggle="pill" data-bs-target="#pills-contact" type="button" role="tab" aria-controls="pills-contact" >BID</button>
                  </li>
                </ul>
              </div> */}
            </div>
          </div>
          <div className="row">
            <div className="col-sm-12 col-md-12">
              <div className="pricefilterwrap">
                <ul className="nav nav-pills pricefilterpills mb-3" id="pills-tab" role="tablist">
                  <li className="nav-item" role="presentation">
                    <button
                      className={`nav-link pricelink ${currentFilter === 'PRICE_LOWEST' ? 'active' : ''}`}
                      id="pills-home-tab"
                      data-bs-toggle="pill"
                      data-bs-target="#price-lowest"
                      type="button"
                      role="tab"
                      aria-controls="pills-home"
                      aria-selected="true"
                      onClick={() => handleFilter('PRICE_LOWEST')}
                    >
                      Price - Lowest
                    </button>
                  </li>
                  <li className="nav-item" role="presentation">
                    <button
                      className={`nav-link pricelink ${currentFilter === 'PRICE_HIGHEST' ? 'active' : ''}`}
                      id="pills-profile-tab"
                      data-bs-toggle="pill"
                      data-bs-target="#price-highest"
                      type="button"
                      role="tab"
                      aria-controls="pills-profile"
                      aria-selected="false"
                      onClick={() => handleFilter('PRICE_HIGHEST')}
                    >
                      Price - Highest
                    </button>
                  </li>
                  {/* Not used in Shibari Collection. Needed only in Genesis. */}
                  {/* <li className="nav-item" role="presentation">
                    <button
                      className={`nav-link pricelink ${currentFilter === 'RARITY_RARE' ? 'active' : ''}`}
                      id="pills-contact-tab"
                      data-bs-toggle="pill"
                      data-bs-target="#rarity-rare"
                      type="button"
                      role="tab"
                      aria-controls="pills-contact"
                      aria-selected="false"
                      onClick={() => handleFilter('RARITY_RARE')}
                    >
                      Rarity - Rare
                    </button>
                  </li>
                  <li className="nav-item" role="presentation">
                    <button
                      className={`nav-link pricelink ${currentFilter === 'RARITY_BASE' ? 'active' : ''}`}
                      id="pills-contact-tab"
                      data-bs-toggle="pill"
                      data-bs-target="#rarity-common"
                      type="button"
                      role="tab"
                      aria-controls="pills-contact"
                      aria-selected="false"
                      onClick={() => handleFilter('RARITY_BASE')}
                    >
                      Rarity - Base
                    </button>
                  </li> */}
                </ul>
              </div>


              {/* Not used in Shibari Collection. Needed only in Genesis. */}
              {/* <div className="vc-filter vc-explore">
                <ul className="nav vc-nav nav-pills">
                  <li className="nav-item vcdropdown pricedropdown dropdown">
                    <div
                      className="nav-link vcdropdown-toggle dropdown-toggle dropbtn"
                      data-bs-toggle="dropdown"
                      role="button"
                      aria-expanded="false"
                    >
                      Filter <img src="assets/images/settings-filter.svg" alt="filter-icon" className="img-fluid" />
                    </div>
                    
                    <div className="dropdown-content">

                      <p>Format</p>

                      <div>
                        <div className="form-check checkblock">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            id="flexCheckDefault"
                            name="mp4"
                            value="mp4"
                            checked={checkedState[0]}
                            onChange={() => handleInnerFilter(0)}
                          />
                          <label className="form-check-label" htmlFor="flexCheckDefault">
                            MP4
                          </label>
                        </div>
                      </div>
                      <div>
                        <div className="form-check checkblock">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            id="flexCheckDefault"
                            name="jpg"
                            value="jpg"
                            checked={checkedState[1]}
                            onChange={() => handleInnerFilter(1)}
                          />
                          <label className="form-check-label" htmlFor="flexCheckDefault">
                            JPG
                          </label>
                        </div>
                      </div>
                      
                      <hr className="seperator" />

                      <p>Rarity</p>

                      <div>
                        <div className="form-check checkblock">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            id="flexCheckDefault"
                            name="base"
                            value="base"
                            checked={checkedState[2]}
                            onChange={() => handleInnerFilter(2)}
                          />
                          <label className="form-check-label" htmlFor="flexCheckDefault">
                            Base
                          </label>
                        </div>
                      </div>
                      <div>
                        <div className="form-check checkblock">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            id="flexCheckDefault"
                            name="rare"
                            value="rare"
                            checked={checkedState[3]}
                            onChange={() => handleInnerFilter(3)}
                          />
                          <label className="form-check-label" htmlFor="flexCheckDefault">
                            Rare
                          </label>
                        </div>
                      </div>
                      <div>
                        <div className="form-check checkblock">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            id="flexCheckDefault"
                            name="epic"
                            value="epic"
                            checked={checkedState[4]}
                            onChange={() => handleInnerFilter(4)}
                          />
                          <label className="form-check-label" htmlFor="flexCheckDefault">
                            Epic
                          </label>
                        </div>
                      </div>
                      <div>
                        <div className="form-check checkblock">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            id="flexCheckDefault"
                            name="legendary"
                            value="legendary"
                            checked={checkedState[5]}
                            onChange={() => handleInnerFilter(5)}
                          />
                          <label className="form-check-label" htmlFor="flexCheckDefault">
                            Legendary
                          </label>
                        </div>
                      </div>
                    
                    </div>

                  </li>
                </ul>
              </div>
             */}
            
            
            </div>
          </div>
        </div>
        {/* Slider */}
        <div className="container">
          <div className="row">
            <div className="ctab-content tab-content" id="pills-tabContent">
              <div
                className="tab-pane fade show active ctabpane"
                id="pills-home"
                role="tabpanel"
                aria-labelledby="pills-home-tab"
              >
                {isLoading ? <PageLoader /> : <NftList data={NFTs} sortOrder={sortOrder} />}
              </div>
              {/* END OF ALL TAB */}
            </div>
          </div>
        </div>
      </section>
    </NftProvider>
  )

};

export default ShibariCollection;
