import React from 'react'
import orderBy from 'lodash/orderBy'
import uuid from 'react-uuid'
import NftCard from './NftCard'


const NftList = (props) => {
  const { data: nfts, sortOrder} = props
  
  return (
    <div className="row">
      {orderBy(nfts, 'sellPriceNum', [sortOrder === 'asc' ? 'asc' : 'desc']).map((nft) => (
        <div className="col-sm-12 col-md-4" key={uuid()}>
          <NftCard nft={nft} />
        </div>
      ))}
      </div>
  )
}

export default NftList
