import React, { useState, useContext, useCallback } from 'react'
import _ from 'lodash';
import { Link } from 'react-router-dom';
import { SellableNft } from 'config/constants/types'
import BigNumber from 'bignumber.js'


interface NftCardProps {
  nft: SellableNft
}

const NftCard: React.FC<NftCardProps> = ({ nft }) => {

  return (
    <div className="vcblock">
      <Link to={{
        pathname: `/shibari-detail/${nft.trade}`
      }}>
        <img src={`/images/nfts/${nft.previewImage}`} alt={nft.name} className="img-fluid" />
      </Link>
      <div className="vcblockhead">
        <Link to={`/shibari-detail/${nft.trade}`}>
          <h6 className="inner-title">{nft.name}</h6>
        </Link>
        <p>By <Link to={{ pathname: nft.authorTwitter }} >{nft.authorName}</Link></p>
        <div className="vcblock-smcontent">
          <p>On Sell for <span className="b">{new BigNumber(nft.sellPrice).div(new BigNumber(10).pow(18)).toFixed()} ALIFE </span></p>
        </div>
      </div>
    </div>
  )
}

export default NftCard
